/* Home.module.css */

.homeSection {
    background-color: #f9f9f9;
    padding: 80px 0;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .home {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .homeContent {
    max-width: 600px;
  }
  
  .homeTitle {
    margin-bottom: 20px;
  }
  
  .homeTitle h2 {
    font-size: 3rem;
    color: #333;
  }
  
  .textWrapper {
    margin-bottom: 30px;
  }
  
  .textWrapper p {
    font-size: 1.5rem;
    font-weight: 500;
    color: #666;
  }
  
  .links {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .link a {
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 600;
    color: #3498db;
    transition: color 0.3s;
  }
  
  .link a:hover {
    color: #2980b9;
  }
  
  .divider {
    font-size: 1.2rem;
    color: #666;
  }
  
  .scroll {
    margin-top: 20px;
  }
  
  .scroll a span {
    display: inline-block;
    font-size: 1.2rem;
    font-weight: 600;
    color: #fff;
    background-color: #3498db;
    border: 2px solid #3498db;
    border-radius: 10px;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    transition: background-color 0.3s;
  }
  
  .scroll a span:hover {
    background-color: #2980b9;
  }
  
  .circ {
    position: relative;
    flex-shrink: 0;
  }
  
  .svgImage {
    max-width: 100%;
    height: auto;
  }
  
  .circle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #3498db;
    border-radius: 50%;
    overflow: hidden;
  }
  
  .content {
    position: relative;
    padding: 20px;
    text-align: center;
  }
  
  .profileImage {
    max-width: 100%;
    height: auto;
    border-radius: 50%;
  }
  
  .hireMeLink {
    display: inline-block;
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1.2rem;
    font-weight: 600;
    color: #fff;
    background-color: #3498db;
    border: 2px solid #3498db;
    border-radius: 10px;
    text-decoration: none;
    transition: background-color 0.3s;
}

.hireMeLink:hover {
  background-color: #2980b9;
}
  