
.contact {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.title h2 {
    text-align: center;
    font-size: 23px;
    font-weight: 800;
    margin-bottom:2.5rem  ;
}

.contact-form {
    grid-template-columns: repeat(2, 1fr);
    padding-top: 50px;
}

.content {
    align-self: start;
}

.content p {
    font-size: 14px;
}

.content h3 {
    font-size: 18px;
    font-weight: 700;
}

.contact-wrapper {
    display: grid;
    grid-template-columns: 70px 200px;
    grid-template-rows: repeat(2, 1fr);
    margin-bottom: 15px;
}

.contact-wrapper > *:first-child {
    grid-row: 1 / span 2;
    align-self: center;
    justify-self: start;
}

.contact-wrapper span {
    font-size: 18px;
    font-weight: 500;
}

.contact-wrapper h3,
.contact-wrapper a {
    font-size: 14px;
    font-weight: 500;
}

.heading h3 {
    font-size: 18px;
    margin-top: -5px;
}

/* Eklenen media query */
@media screen and (max-width: 768px) {
    .contact-form {
        grid-template-columns: 1fr;
    }

    .title h2 {
        font-size: 2.5rem;
    }

    .content p {
        font-size: 18px;
    }

    .content h3 {
        font-size: 1.4rem;
        padding-top: 10px;
    }

    .contact-wrapper {
        grid-template-columns: 1fr;
        grid-template-rows: 70px 1fr;
    }

    .contact-wrapper > *:first-child {
        grid-row: 1;
        grid-column: 1;
        align-self: start;
        justify-self: start;
    }

    .contact-wrapper span,
    .contact-wrapper h3,
    .contact-wrapper a {
        font-size: 18px;
    }

    .heading {
        font-size: 1.2rem;
    }
}
