/* Profile.module.css */

.list2 {
  margin: 16px auto;
  width: 100%;
  max-width: 550px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
  transition: 0.3s ease-in-out;
  overflow: hidden;
  position: relative;
  
}

.list2:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
}

.pos {
  
  display: flex;
  justify-content: center;
  align-items: center;
}

.school-pos {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.school-logo {
  height: auto;
  width: 150px;
  object-fit: cover;
  margin-right: 27px;
}

.position2 {
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 18px;
}

.position2 .et {
  font-weight: 500;
  margin-bottom: 27px;
}

.position2 .et .school {
  font-weight: 600;
  font-size: 18px;
}

.position2 .work2 {
  font-weight: 500;
  font-size: 14px;
}

.position2 .time {
  font-size: 14px;
  font-weight: 500;
}

.position2 .work2 {
  width: 100%;
  font-weight: 400;
}


.position2 .field {
  font-weight: 500;
  font-size: 18px;
}

.experience2 {
  margin-top: 16px;
  margin-bottom: 16px;
  position: relative;
}

.title2 {
  font-weight: 800;
  font-size: 23px;
  margin-bottom: 2.5rem;
  margin-top: 2.5rem;
  text-align: center;
}

.details2 {
  margin: 0 auto;
  width: 100%;
}

.details2 ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-left: 20px;
}

.timeline-container {
  position: absolute;
  left: -10px;
  top: 0;
  height: 100%;
}

.timeline {
  width: 10px;
  height: 100%;
  background-color: #d8bb3d;
  transform-origin: top;
}

@media screen and (max-width: 768px) {
  .list2 {
    flex-direction: column;
    align-items: flex-start;
  }

  .details2 {
    width: 100%;
  }

  .school-logo {
    max-width: 100%;
    height: auto;
    margin-right: 0;
    margin-bottom: 10px;
  }
}
