/* YourNewFile.module.css */

.list {
  margin: 16px auto;
  width: 100%;
  max-width: 550px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
  transition: 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
}


.dark-bg {
  background-color: rgba(0, 0, 0, 0.5);
padding: 1px;
}

.company-pos {
  background-color: var(--bg-light2);
  padding-top: 15px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;

}

.list .company-logo {
  height: auto;
  width: 120px;
  object-fit:cover;
}

.list:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
}

.position {
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 12px;
}

.position a {
  font-weight: 500;
  text-decoration: none;
}

.position span {
  font-weight: 500;
}

.position .time {
  font-size: 14px;
  font-weight: 500;
}

.position .company {
  font-weight: 600;
  font-size: 20px;
}

.position .ta {
  font-size: 14px;
  font-weight: 500;
}

.work {
  
  padding-left: 20px;
  padding-right: 10px;
  color: black;
  font-size: 14px;
  width: 100%;
  font-weight: 500;
  margin-top: 18px;
  margin-bottom: 18px;
}

.experience {
  margin-top: 12px;
  margin-bottom: 12px;
}

.title {
  font-weight: 800;
  font-size: 23px;
  margin-bottom: 2.5rem;
  margin-top: 2.5rem;
  text-align: center;
}

.details {
  margin: 0 auto;
  position: relative;
}

.details ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-left: 20px;
}

@media screen and (max-width: 768px) {
  .list {
    width: 100%;
    max-width: none;
    align-items: flex-start;
  }

  .details {
    width: 100%;
  }
}
