.form-control > :nth-child(odd) {
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 500;
    font-size: 14px; /* Daha küçük yazı boyutu */
    border-radius: 8px; /* Daha yuvarlatılmış köşeler */
    line-height: 14px; /* Artan çizgi yüksekliği */
    border: 2px solid #ccc;
    padding: 10px; /* Daha küçük iç boşluklar */
    margin-top: 10px; /* Artan üst boşluk */
    margin-bottom: 10px; /* Daha az alt boşluk */
    width: 100%;
    outline: none;
    box-sizing: border-box;
    transition: all 0.3s;
}

.form-control > :nth-child(odd)::placeholder {
    color: #aaa;
}

.textarea {
    height: 120px; /* Daha küçük textarea yüksekliği */
    line-height: 120px; /* Daha küçük textarea çizgi yüksekliği */
    resize: vertical;
}

.button {
    font-family: 'Josefin Sans', sans-serif;
    width: 130px; /* Daha geniş buton */
    height: 25px; /* Daha yüksek buton */
    background-color: #111; /* Daha koyu arka plan renk */
    color: #fff;
    border-radius: 8px;
    border: 0;
    cursor: pointer;
    font-size: 14px; /* Daha büyük yazı boyutu */
    padding: 5px;
    transition: all 0.3s;
}

.button:hover {
    transform: scale(0.98);
}

.invalid input,
.invalid textarea {
    border: 1px solid var(--danger-color) !important;
    background-color: #fddddd;
}

.invalid input:focus,
.invalid textarea:focus {
    border-color: #ff8800;
    background-color: #fbe8d2;
}

.error {
    color: var(--danger-color);
    font-size: 14px;
}

@media screen and (max-width: 600px) {
    .button {
        width: 100%;
    }
}
