.footer {
    background-color: black;
    color: #ddd;
    margin-top: 2.5rem;
    padding: 2.5rem;
    font-size: 14px;
}

.flex {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    text-align: center;
}

.flex img {
    width: 65px;
    border-radius: 50%;
    margin-bottom: 10px;
}

.footer ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
}

.footer ul li {
    margin-bottom: 10px;
}

.footer ul li a {
    padding: 15px;
    color: #fff;
    transition: color 0.5s;
    text-decoration: none;
}

.footer ul li a:hover {
    color: #d8bb3d;
}

.icons a {
    color: #fff;
    margin: 0 15px;
}

.icons a:hover {
    color: #d8bb3d;
}

@media screen and (min-width: 768px) {
    .flex {
        flex-direction: row;
    }

    .footer ul {
        flex-direction: row;
    }

    .footer ul li {
        margin-bottom: 0;
        margin-right: 20px;
    }
}
