
:root {
  --bg-dark: #0f141e;
  --bg-light: #f9f9f9;
  --tw-bg-opacity: 1;
  --bg-light2: rgb(245 245 245 / var(--tw-bg-opacity));
  --dark-color: rgba(15, 20, 30, 0.8);
  --danger-color: #ea4343;
}

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--bg-light);
  font-family: "Josefin Sans", sans-serif;
  color: #000;
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.5;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  overflow: auto;
  padding: 0 20px;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  margin: 10px 0;
  font-weight: 500;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Josefin Sans', sans-serif;
}

.reveal {
  transform: translateY(200px);
  opacity: 0;
  transition: all 2s ease;
}

.show {
  transform: translateY(0px);
  opacity: 1;
}

.grid {
  display: grid;
  grid-template-columns: 3fr 4fr;
  justify-content: space-between;
  align-items: center;
  gap: 100px;
  height: 100%;
  padding: 40px 0;
}

.active {
  color: var(--danger-color);
}

.scroll-top {
  position: sticky;
  bottom: 20;
  right: 20;
  z-index: 100;
}

.bg {
  display: flex;
  justify-content: space-around;
}

/* Responsive tasarım için media query ekleyin */
@media screen and (max-width: 768px) {
  .list, .list2 {
    width: 100%;
    max-width: 100%;
  }
  .bg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .list, .list2 {
    width: 100%;
    max-width: 100%;
  }
}

@media screen and (max-width: 768px) {
  body {
    text-align: justify;
  }

  .grid {
    grid-template-columns: 1fr;
  }
}
