.header {
    position: fixed;
    width: 100%;
    z-index: 1000;
    transition: background-color 0.3s;
  }
  
  .navbar {
    background-color: var(--bg-light);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    padding: 20px 0;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .navbarWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .icons {
    display: flex;
    gap: 10px;
  }
  
  .icons a {
    text-decoration: none;
    color: #333;
    transition: color 0.3s;
  }
  
  .icons a:hover {
    color: #3498db;
  }
  
  .navbarLinks {
    display: flex;
    align-items: center;
  }
  
  .navbarLinks ul {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
  }
  
  .navbarLinks a {
    text-decoration: none;
    color: #333;
    font-size: 1rem;
    transition: color 0.3s;
  }
  
  .navbarLinks a:hover {
    color: #3498db;
  }
  
  .toggleButton {
    display: none;
    flex-direction: column;
    cursor: pointer;
  }
  
  .toggleButton span {
    display: block;
    width: 30px;
    height: 4px;
    background-color: #333;
    margin: 6px 0;
    transition: background-color 0.3s;
  }
  
  .toggleButton.open span {
    background-color: #3498db;
  }
  
  @media (max-width: 899px) {
    .toggleButton {
      display: flex;
    }
  
    .navbarLinks {
      flex-direction: column;
      align-items: flex-start;
      position: absolute;
      top: 60px;
      left: 0;
      background-color: #fff;
      width: 100%;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
      padding: 20px;
      transition: opacity 0.3s;
      opacity: 0;
      pointer-events: none;
    }
  
    .navbarLinks.show {
      opacity: 1;
      pointer-events: all;
    }
  
    .navbarLinks ul {
      flex-direction: column;
      gap: 10px;
    }
  
    .navbarLinks a {
      font-size: 1.2rem;
    }
  }
  