.aboutSection {
    padding: 80px 0;
  }
  
  .about {
    display: flex;
    align-items: center;
    justify-content: space-around;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .aboutImg img {
    border-radius: 50%;
    max-width: 250px; /* Adjusted max-width for a larger image */
    border: 4px solid #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .aboutContent {
    max-width: 600px;
    text-align: left;
    padding: 20px;
  }
  
  .aboutHeader {
    font-size: 2rem;
    color: #333;
  }
  
  .aboutText {
    font-size: 1.1rem;
    color: #666;
    line-height: 1.6;
  }
  
  .name {
    font-weight: bold;
    color: #3498db;
  }
  
  .border {
    border-bottom: 2px solid #3498db;
    padding-bottom: 10px;
    margin-bottom: 20px;
  }
  